.App{
  width:100vw;
  height:100vh;
  display:grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(3,1fr);
}
.App-toggle{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.pr1{
  width:15rem;
  aspect-ratio:1;
  background-color:black;
  border-radius: 50%;
  place-self: center;
  margin:3em;
}
.pr1:hover{
  transform: scale(1.1);
}
