@media only screen and (max-width: 600px) {
  .App{
    width:100vw;
    height:auto;
    display:grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: repeat(1,1fr);
  }
  .App-toggle{
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 1fr 1fr;
    place-items: center;
  }
}
